html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/*
 * Font Guide: To get [Font Name] use the corresponding rules.
 *
 * Proxima Nova Black
 * font-family: proxima-nova, sans-serif;
 * font-weight: 900;
 * font-style: normal;
 *
 * Proxima Nova Extrabold
 * font-family: proxima-nova, sans-serif;
 * font-weight: 800;
 * font-style: normal;
 *
 * Proxima Nova Regular
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: normal;
 *
 * Proxima Nova Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: italic;
 *
 * Proxima Nova Light
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: normal;
 *
 * Proxima Nova Light Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: italic;
 *
 * Proxima Nova Medium Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 500;
 * font-style: italic;
 */
html {
  color: #0f1422;
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
}
body {
  color: #0f1422;
  font-size: 1.6rem;
}
h1 {
  font-family: proxima-nova, sans-serif;
  font-size: 9rem;
  font-style: normal;
  font-weight: 300;
  line-height: 9rem;
}
h2 {
  font-family: proxima-nova, sans-serif;
  font-size: 6.7rem;
  font-style: normal;
  font-weight: 300;
  line-height: 6.2rem;
}
h3 {
  font-family: proxima-nova, sans-serif;
  font-size: 5.1rem;
  font-style: normal;
  font-weight: 300;
  line-height: 5.1rem;
}
h4 {
  font-family: proxima-nova, sans-serif;
  font-size: 3.9rem;
  font-style: normal;
  font-weight: 800;
  line-height: 3.9rem;
}
h5 {
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.8rem;
}
h6 {
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.1rem;
}
p,
span {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
p.small,
span.small {
  font-size: 1.4rem;
  line-height: 2.2rem;
}
p.large,
span.large {
  font-size: 1.9rem;
  line-height: 2.6rem;
}
blockquote {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 400;
  line-height: 2.8rem;
}
ul,
ol {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
.subheading {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 800;
}
.subheading.small {
  font-size: 1.2rem;
  text-transform: uppercase;
}
.hero {
  font-family: proxima-nova, sans-serif;
  font-size: 12rem;
  font-style: normal;
  font-weight: 900;
  line-height: 10rem;
}
.hero.large {
  font-size: 15rem;
  line-height: 12rem;
}
.pull-quote {
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: italic;
  font-weight: 500;
  line-height: 4rem;
}
.caption {
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: italic;
  font-weight: 300;
  line-height: 2.2rem;
}
.caption.small {
  font-size: 1.6rem;
}
.primary-text {
  opacity: 0.87;
}
.primary-text.inverted {
  opacity: 1;
}
.secondary-text {
  opacity: 0.6;
}
.secondary-text.inverted {
  opacity: 0.7;
}
.help-text {
  opacity: 0.38;
}
.help-text.inverted {
  opacity: 0.5;
}
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select
 */
button {
  margin: 0;
  padding: 0;
}
.ce-button {
  border-radius: 3.5rem;
  border-width: 0;
  display: flex;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 900;
  justify-content: center;
  text-transform: uppercase;
}
@media screen and (max-width: 768px) {
  .ce-button {
    width: 100%;
  }
}
.ce-button .ce-button__label {
  flex-grow: 1;
}
.ce-button.ce-button--small .ce-button__label {
  border-radius: 3.5rem;
  display: inline-block;
  font-size: 1.2rem;
  margin: 0.2rem;
  padding: 0.85rem 3.8rem;
}
.ce-button.ce-button--large .ce-button__label {
  border-radius: 3.5rem;
  display: inline-block;
  font-size: 1.6rem;
  margin: 0.2rem;
  padding: 1.3rem 5.8rem;
}
.ce-button.ce-button--jumbo .ce-button__label {
  border-radius: 3.5rem;
  display: inline-block;
  font-size: 1.6rem;
  margin: 0.2rem;
  padding: 2.3rem 5.8rem;
}
.ce-button:focus {
  box-shadow: 0 0 0 5px #b2e7ff;
  outline: none;
}
.ce-button--outline.ce-button--small .ce-button__label {
  background: #fff;
}
.ce-button--outline.ce-button--large .ce-button__label {
  background: #fff;
}
.ce-button--outline.ce-button--jumbo .ce-button__label {
  background: #fff;
}
.ce-button--text.ce-button--small .ce-button__label {
  margin: 0;
  padding: 1.05rem 4rem;
}
.ce-button--text.ce-button--large .ce-button__label {
  margin: 0;
  padding: 1.5rem 6rem;
}
.ce-button--text.ce-button--jumbo .ce-button__label {
  margin: 0;
  padding: 2.5rem 6rem;
}
.ce-button--text:focus {
  box-shadow: none;
}
.ce-button--text:focus .ce-button__label {
  text-decoration: underline;
}
.ce-button--primary {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
  color: #fff;
}
.ce-button--primary:hover {
  background: #007ab2;
}
.ce-button--primary:active {
  background: #00577f;
}
.ce-button--primary.ce-button--disabled {
  background: #ccc;
}
.ce-button--primary.ce-button--outline {
  color: #000;
}
.ce-button--primary.ce-button--outline:hover {
  color: #007ab2;
}
.ce-button--primary.ce-button--outline:active {
  color: #00577f;
}
.ce-button--primary.ce-button--outline.ce-button--disabled {
  color: #ccc;
}
.ce-button--primary.ce-button--text {
  background: #fff;
  color: #007ab2;
}
.ce-button--primary.ce-button--text:hover {
  color: #008dd2;
}
.ce-button--primary.ce-button--text:active {
  color: #006999;
}
.ce-button--primary.ce-button--text:focus {
  color: #006999;
}
.ce-button--primary.ce-button--text.ce-button--disabled {
  color: #ccc;
}
.ce-button--secondary {
  background: linear-gradient(317.58deg, #62248d 0%, #c4356e 100%);
  color: #fff;
}
.ce-button--secondary:hover {
  background: #b3003a;
}
.ce-button--secondary:active {
  background: #80002a;
}
.ce-button--secondary.ce-button--disabled {
  background: #ccc;
}
.ce-button--secondary.ce-button--outline {
  color: #000;
}
.ce-button--secondary.ce-button--outline:hover {
  color: #b3003a;
}
.ce-button--secondary.ce-button--outline:active {
  color: #80002a;
}
.ce-button--secondary.ce-button--outline.ce-button--disabled {
  color: #ccc;
}
.ce-button--secondary.ce-button--text {
  background: #fff;
  color: #982d7c;
}
.ce-button--secondary.ce-button--text:hover {
  color: #bc178f;
}
.ce-button--secondary.ce-button--text:active {
  color: #900086;
}
.ce-button--secondary.ce-button--text:focus {
  color: #900086;
}
.ce-button--secondary.ce-button--text.ce-button--disabled {
  color: #ccc;
}
.ce-button--black {
  background: #000;
  color: #fff;
}
.ce-button--black:hover {
  background: #404040;
}
.ce-button--black:active {
  background: #262626;
}
.ce-button--black.ce-button--disabled {
  background: #ccc;
}
.ce-button--black.ce-button--outline {
  color: #000;
}
.ce-button--black.ce-button--outline:hover {
  color: #404040;
}
.ce-button--black.ce-button--outline:active {
  color: #262626;
}
.ce-button--black.ce-button--outline.ce-button--disabled {
  color: #ccc;
}
.ce-button--black.ce-button--text {
  background: #fff;
  color: #000;
}
.ce-button--black.ce-button--text:hover {
  color: #404040;
}
.ce-button--black.ce-button--text:active {
  color: #262626;
}
.ce-button--black.ce-button--text:focus {
  color: #262626;
}
.ce-button--black.ce-button--text.ce-button--disabled {
  color: #ccc;
}
.ce-button--inverted {
  background: #fff;
  color: #000;
}
.ce-button--inverted:hover {
  color: #404040;
}
.ce-button--inverted:active {
  color: #262626;
}
.ce-button--inverted.ce-button--disabled {
  background: #ccc;
}
.ce-button--inverted.ce-button--outline {
  background: #fff;
  color: #fff;
}
.ce-button--inverted.ce-button--outline .ce-button__label {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
}
.ce-button--inverted.ce-button--outline:hover .ce-button__label {
  background: #4a4396;
}
.ce-button--inverted.ce-button--outline:active .ce-button__label {
  background: #3e3e85;
}
.ce-button--inverted.ce-button--outline.ce-button--disabled {
  color: #ccc;
}
.ce-button--inverted.ce-button--text {
  color: #fff;
  background: transparent;
}
.ce-button--inverted.ce-button--text:hover {
  background: #404040;
}
.ce-button--inverted.ce-button--text:active {
  background: #262626;
}
.ce-button--inverted.ce-button--text:focus {
  background: #262626;
}
.ce-button--inverted.ce-button--text.ce-button--disabled {
  color: #ccc;
}
