html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strong,
sub,
sup,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/*
 * Font Guide: To get [Font Name] use the corresponding rules.
 *
 * Proxima Nova Black
 * font-family: proxima-nova, sans-serif;
 * font-weight: 900;
 * font-style: normal;
 *
 * Proxima Nova Extrabold
 * font-family: proxima-nova, sans-serif;
 * font-weight: 800;
 * font-style: normal;
 *
 * Proxima Nova Regular
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: normal;
 *
 * Proxima Nova Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 400;
 * font-style: italic;
 *
 * Proxima Nova Light
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: normal;
 *
 * Proxima Nova Light Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 300;
 * font-style: italic;
 *
 * Proxima Nova Medium Italic
 * font-family: proxima-nova, sans-serif;
 * font-weight: 500;
 * font-style: italic;
 */
html {
  color: #0f1422;
  font-family: proxima-nova, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
}
body {
  color: #0f1422;
  font-size: 1.6rem;
}
h1 {
  font-family: proxima-nova, sans-serif;
  font-size: 9rem;
  font-style: normal;
  font-weight: 300;
  line-height: 9rem;
}
h2 {
  font-family: proxima-nova, sans-serif;
  font-size: 6.7rem;
  font-style: normal;
  font-weight: 300;
  line-height: 6.2rem;
}
h3 {
  font-family: proxima-nova, sans-serif;
  font-size: 5.1rem;
  font-style: normal;
  font-weight: 300;
  line-height: 5.1rem;
}
h4 {
  font-family: proxima-nova, sans-serif;
  font-size: 3.9rem;
  font-style: normal;
  font-weight: 800;
  line-height: 3.9rem;
}
h5 {
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.8rem;
}
h6 {
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: normal;
  font-weight: 800;
  line-height: 2.1rem;
}
p,
span {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
p.small,
span.small {
  font-size: 1.4rem;
  line-height: 2.2rem;
}
p.large,
span.large {
  font-size: 1.9rem;
  line-height: 2.6rem;
}
blockquote {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: italic;
  font-weight: 400;
  line-height: 2.8rem;
}
ul,
ol {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 2.2rem;
}
.subheading {
  font-family: proxima-nova, sans-serif;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 800;
}
.subheading.small {
  font-size: 1.2rem;
  text-transform: uppercase;
}
.hero {
  font-family: proxima-nova, sans-serif;
  font-size: 12rem;
  font-style: normal;
  font-weight: 900;
  line-height: 10rem;
}
.hero.large {
  font-size: 15rem;
  line-height: 12rem;
}
.pull-quote {
  font-family: proxima-nova, sans-serif;
  font-size: 2.8rem;
  font-style: italic;
  font-weight: 500;
  line-height: 4rem;
}
.caption {
  font-family: proxima-nova, sans-serif;
  font-size: 2.1rem;
  font-style: italic;
  font-weight: 300;
  line-height: 2.2rem;
}
.caption.small {
  font-size: 1.6rem;
}
.primary-text {
  opacity: 0.87;
}
.primary-text.inverted {
  opacity: 1;
}
.secondary-text {
  opacity: 0.6;
}
.secondary-text.inverted {
  opacity: 0.7;
}
.help-text {
  opacity: 0.38;
}
.help-text.inverted {
  opacity: 0.5;
}
/* Grayscale
 * #fff - #000 in [various]% steps
 * See https://talloux.invisionapp.com/console/Design-System-ck70x3ivn2cy2017xjpu68gy4/ck70x4duz0046013ojf58m5ig/inspect
 */
/* Checked/Unchecked Disabled Radio,
 * Checked/Unchecked Disabled Checkbox,
 * Disabled Select
 */
.ce-toggle-button-group {
  border-radius: 3.5rem;
  border-width: 0;
  display: inline-block;
}
.ce-toggle-button-group legend {
  display: none;
}
.ce-toggle-button-group.ce-toggle-button-group--small .ce-toggle-button label {
  padding: 0.9rem 1.33rem 0.5rem 1.33rem;
}
.ce-toggle-button-group.ce-toggle-button-group--small .ce-toggle-button:first-of-type label {
  padding-left: 2.33rem;
}
.ce-toggle-button-group.ce-toggle-button-group--small .ce-toggle-button:last-of-type label {
  padding-right: 2.33rem;
}
.ce-toggle-button-group.ce-toggle-button-group--large .ce-toggle-button label {
  padding: 1.4rem 2rem 1.1rem 2rem;
}
.ce-toggle-button-group.ce-toggle-button-group--large .ce-toggle-button:first-of-type label {
  padding-left: 3rem;
}
.ce-toggle-button-group.ce-toggle-button-group--large .ce-toggle-button:last-of-type label {
  padding-right: 3rem;
}
.ce-toggle-button-group.ce-toggle-button-group--jumbo .ce-toggle-button label {
  padding: 2.4rem 2rem 2.1rem 2rem;
}
.ce-toggle-button-group.ce-toggle-button-group--jumbo .ce-toggle-button:first-of-type label {
  padding-left: 3rem;
}
.ce-toggle-button-group.ce-toggle-button-group--jumbo .ce-toggle-button:last-of-type label {
  padding-right: 3rem;
}
.ce-toggle-button-group--primary {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
}
.ce-toggle-button-group--primary .ce-toggle-button {
  background: #fff;
}
.ce-toggle-button-group--primary .ce-toggle-button label .ce-icon {
  stroke: #000;
}
.ce-toggle-button-group--primary .ce-toggle-button:hover {
  background: transparent;
}
.ce-toggle-button-group--primary .ce-toggle-button:hover label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--primary .ce-toggle-button:active {
  background: #00577f;
}
.ce-toggle-button-group--primary .ce-toggle-button:active label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--primary .ce-toggle-button.ce-toggle-button--checked {
  background: transparent;
}
.ce-toggle-button-group--primary .ce-toggle-button.ce-toggle-button--checked label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--secondary {
  background: linear-gradient(317.58deg, #62248d 0%, #c4356e 100%);
}
.ce-toggle-button-group--secondary .ce-toggle-button {
  background: #fff;
}
.ce-toggle-button-group--secondary .ce-toggle-button label .ce-icon {
  stroke: #000;
}
.ce-toggle-button-group--secondary .ce-toggle-button:hover {
  background: transparent;
}
.ce-toggle-button-group--secondary .ce-toggle-button:hover label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--secondary .ce-toggle-button:active {
  background: #80002a;
}
.ce-toggle-button-group--secondary .ce-toggle-button:active label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--secondary .ce-toggle-button.ce-toggle-button--checked {
  background: transparent;
}
.ce-toggle-button-group--secondary .ce-toggle-button.ce-toggle-button--checked label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--black {
  background: #000;
}
.ce-toggle-button-group--black .ce-toggle-button {
  background: #fff;
}
.ce-toggle-button-group--black .ce-toggle-button label .ce-icon {
  stroke: #000;
}
.ce-toggle-button-group--black .ce-toggle-button:hover {
  background: transparent;
}
.ce-toggle-button-group--black .ce-toggle-button:hover label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--black .ce-toggle-button:active {
  background: #262626;
}
.ce-toggle-button-group--black .ce-toggle-button:active label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--black .ce-toggle-button.ce-toggle-button--checked {
  background: transparent;
}
.ce-toggle-button-group--black .ce-toggle-button.ce-toggle-button--checked label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--inverted {
  background: #fff;
}
.ce-toggle-button-group--inverted .ce-toggle-button {
  background: linear-gradient(317.58deg, #6645ad 0%, #2d71be 100%);
}
.ce-toggle-button-group--inverted .ce-toggle-button label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--inverted .ce-toggle-button:hover {
  background: #4a4396;
}
.ce-toggle-button-group--inverted .ce-toggle-button:hover label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--inverted .ce-toggle-button:active {
  background: #3e3e85;
}
.ce-toggle-button-group--inverted .ce-toggle-button:active label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--inverted .ce-toggle-button.ce-toggle-button--checked {
  background: #fff;
}
.ce-toggle-button-group--inverted .ce-toggle-button.ce-toggle-button--checked label .ce-icon {
  stroke: #000;
}
.ce-toggle-button-group--disabled {
  background: #ccc;
  pointer-events: none;
}
.ce-toggle-button-group--disabled .ce-toggle-button {
  background: #fff;
}
.ce-toggle-button-group--disabled .ce-toggle-button label .ce-icon {
  stroke: #ccc;
}
.ce-toggle-button-group--disabled .ce-toggle-button:hover {
  background: transparent;
}
.ce-toggle-button-group--disabled .ce-toggle-button:hover label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--disabled .ce-toggle-button:active {
  background: #ccc;
}
.ce-toggle-button-group--disabled .ce-toggle-button:active label .ce-icon {
  stroke: #fff;
}
.ce-toggle-button-group--disabled .ce-toggle-button.ce-toggle-button--checked {
  background: transparent;
}
.ce-toggle-button-group--disabled .ce-toggle-button.ce-toggle-button--checked label .ce-icon {
  stroke: #fff;
}
